/* Murmurations Node */

#murmurations-node {
    fieldset {
        width: 100%;
        padding-bottom: 1rem;
    }

    .components-base-control__label {
        text-transform: initial;
    }

    .components-snackbar__content {
        white-space: pre-line;
    }
    
    .align-start {
        align-items: flex-start;
    }

    .self-start {
        align-self: flex-start;
    }
    
    .justify-inherit {
        justify-content: inherit;
    }

    .gap-5 {
        gap: 5px;
    }

    .p-0 {
        padding: 0;
    }

    .border-b-0 {
        border-bottom: 0;
    }

    .mt-10 {
        margin-top: 10px;
    }

    .mt-20 {
        margin-top: 20px;
    }

    .image-remove {
        align-self: center;
        margin-left: 1rem;
    }

    .label-fieldset {
        display: inline-block;
        font-size: 11px;
        font-weight: 500;
        line-height: 1.4;
        margin-bottom: 8px;
    }
    
    .location-search {
        padding: 1.5rem 1rem;
        margin-left: 10px;
        margin-bottom: 15px;
    }
    
    .test-api {
        margin-top: 1rem;
    }

    .align-left {
        justify-content: start;
        gap: 1rem;
    }
}